import React, {Component} from 'react';
import {observer} from 'mobx-react';
import {Input, Row, Col, Form, Upload, Select, Button, Icon, Cascader, message} from 'antd';
import * as mobx from 'mobx';
import Config from "@/config";
import {requestPost} from "@/utils/api";
import Schemas from "@/utils/Schemas/requirement";
import RequirementRead from './RequirementRead';
//import GlobalStore from "@/store/GlobalStore";

const {Option} = Select;
const {TextArea} = Input;

@observer
class RequirementAdd extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			levelData: ['', '', ''], // 
			fileList: mobx.toJS(this.store.addFields.attachments), // 
			productData: ['', '', '', '', ''],  // 
		}
	}

	componentDidMount() {

	}

	handleChange = (type, e) => {
		this.store.addFields[type] = e.target.value;
		if (type == 'title') {
			this.store.word = e.target.value;
			this.props.getSimilarityRequirementPage();
		}
	}

	onChange = (type, value) => {
		this.store.addFields[type] = value;
	}

	// 
	handleBlur = () => {
		const requirement = JSON.parse(JSON.stringify(this.store.addFields))
		// 
		if (requirement.title == "" || requirement.title == null) {
			return false;
		}
		delete requirement.attachments;
		const param = {
			type: 1,
			requirement: requirement
		}
		this.store.saveRequirement(param);
	}

	// 
	onClick = () => {
		const requirement = JSON.parse(JSON.stringify(this.store.addFields))
		let str = ""
		if (requirement.title == "" || requirement.title == null) {
			str += " "
			message.warn(str);
			return false;
		}
		delete requirement.attachments;
		const param = {
			type: 0,
			requirement: requirement
		}
		this.store.saveRequirement(param).then((res) => {
			const data = res;
			if (data == undefined) {
				message.error('')
			} else {
				message.success("");
				// 
				this.store.addFields = Object.assign({}, {
					"title": "",
					"category": [],
					"level": "",
					"product": "",
					"demandName": "",
					"demandPhone": "",
					"demandStation": "",
					"description": "",
					"attachments": [],
					"id": ""
				});
				this.props.getTableData();
			}
		});
	}

	// 
	fileChange = info => {
		const status = info.file.status;
		if (status === 'done') {
			// 
			message.success(`${info.file.name}`);
		} else if (status === 'error') {
			message.error(`${info.file.name}`);
		} else if (status === 'removed') {
			console.log('2')
		}

		let fileList = [...info.fileList];
		fileList = JSON.parse(JSON.stringify(fileList));
		fileList = fileList.filter((file) => {
			if (file.status === 'error') {
				return false;
			}
			return true;
		});
		fileList = fileList.map(file => {
			if (file.response) {
				file.url = file.response.body[0].fileURL;
			}
			return file;
		});
		fileList = fileList.filter((file) => {
			if (file.response) {
				return file.response.status === 'ok';
			}
			return true;
		});
		this.store.addFields.attachments = Object.assign([], [...fileList])
	}


	render() {
		let _this = this;
		const {levelData, productData} = _this.state;
		const formItemLayout1 = {
			labelCol: {span: 2},
			wrapperCol: {span: 22},
		}
		const options = [
			{
				value: 'bug',
				label: 'bug',
				children: null
			},
			{
				value: '',
				label: '',
				children: [
					{
						value: '',
						label: '',
					},
					{
						value: '',
						label: '',
					},
					{
						value: '',
						label: '',
					},
					{
						value: '',
						label: '',
					}
				],
			},
			{
				value: '',
				label: '',
				children: [
					{
						value: '',
						label: '',
					},
					{
						value: '',
						label: '',
					},
					{
						value: '',
						label: '',
					},
					{
						value: '',
						label: '',
					}
				],
			},
		];
		const props = {
			data: {
				type: 1,    // 
				sourceId: _this.store.addFields.id // id
			},
			name: 'file',
			multiple: true,
			action: Config.management.multiUpload,
			onChange: _this.fileChange,
			onRemove: file => {
				return new Promise((resolve, reject) => {
					requestPost('delAttachment', Config.management.delAttachment, Schemas.requirement.delAttachment, {id: file.response.body[0].id})
					.then((res) => {
						if (res == true) {
							message.success("")
							resolve(res);
						} else {
							message.error('')
							resolve(res);
						}
					});
				});
			},
			fileList: mobx.toJS(this.store.addFields.attachments)
		}
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const { getFieldDecorator } = this.props.form;
		return (
			<div className={this.props.visible ? "container-fluid pr" : "hidden"}>
				<Form layout={'inline'}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label="" {...formItemLayout}>
								{getFieldDecorator('email', {
									rules: [
										{
											required: true,
											message: '',
										},
									],
								})(<Input placeholder="..."
								          onBlur={this.handleBlur}
								          onChange={this.handleChange.bind(this, 'title')}
								          value={this.store.addFields.title}
								/>)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label="" {...formItemLayout}>
								<Select
									placeholder="..."
									onChange={this.onChange.bind(this, 'product')}
									onBlur={this.handleBlur}
									style={{width: '100%'}}
									value={this.store.addFields.product}
								>
									{productData.map((item, index) => {
										return (<Option key={'product' + index} value={item}>{item}</Option>)
									})}
								</Select>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label={''} {...formItemLayout}>
								<Cascader
									placeholder="..."
									options={options}
									expandTrigger="click"
									onBlur={this.handleBlur}
									onChange={this.onChange.bind(this, 'category')}
									value={this.store.addFields.category}
								/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label="" {...formItemLayout}>
								<Select
									placeholder="..."
									onChange={this.onChange.bind(this, 'level')}
									onBlur={this.handleBlur}
									style={{width: '100%'}}
									value={this.store.addFields.level}
								>
									{levelData.map((item, index) => {
										return (<Option key={'level' + index} value={item}>{item}</Option>)
									})}
								</Select>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label="" {...formItemLayout}>
								<Input placeholder=""
								       onBlur={this.handleBlur}
								       onChange={this.handleChange.bind(this, 'demandName')}
								       value={this.store.addFields.demandName}
								/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label="" {...formItemLayout}>
								<Input placeholder=""
								       onBlur={this.handleBlur}
								       onChange={this.handleChange.bind(this, 'demandPhone')}
								       value={this.store.addFields.demandPhone}
								/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item className="w" label="" {...formItemLayout}>
								<Input placeholder=""
								       onBlur={this.handleBlur}
								       onChange={this.handleChange.bind(this, 'demandStation')}
								       value={this.store.addFields.demandStation}
								/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={24} lg={24} xl={24}>
							<Form.Item className="w require-des" label="" {...formItemLayout1}>
								<TextArea
									placeholder=""
									onBlur={this.handleBlur}
									id="box"
									onChange={this.handleChange.bind(this, 'description')}
									value={this.store.addFields.description}
									rows={4}/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={24} lg={24} xl={24}>
							<Form.Item className="w require-des" label="" {...formItemLayout1}>
								<Upload.Dragger {...props}>
									<p className="ant-upload-drag-icon">
										<span className="require-upload"></span><span style={{color: '#8C8C8C'}}></span><span
										style={{color: '#268DFF', verticalAlign: 'top'}}></span>
									</p>
								</Upload.Dragger>
							</Form.Item>
						</Col>
					</Row>
				</Form>
				<div className="require-addSub">
          <span className="title" style={{
	          color: '#268DFF', fontSize: '13px'
          }}></span>
					<Button type="primary" className="mr25" onClick={this.onClick}></Button>
				</div>
				<RequirementRead
					store={this.store}
				/>
			</div>
		);
	}
}

const WrappedRegistrationForm = Form.create({})(RequirementAdd);
export default WrappedRegistrationForm;



