import React from 'react';
import {observer} from 'mobx-react';
import {Select, message, Modal, Checkbox, Input, Icon, Spin, Empty} from 'antd';
import * as mobx from 'mobx';
import moment from 'moment';
import {Scrollbars} from 'react-custom-scrollbars';
import InfiniteScroll from 'react-infinite-scroller';

import RequirementAdd from '../../components/requirement/RequirementAdd';
import RequirementEdit from '../../components/requirement/RequirementEdit';
import _ from 'lodash';
import Config from "@/config";
import Schemas from 'utils/Schemas/requirement';
import {requestPost} from 'utils/api';
import RequirementStore from 'store/requirement/RequirementStore';

const {Option} = Select;
const CheckboxGroup = Checkbox.Group;
const Search = Input.Search;
const confirm = Modal.confirm;
const typeColors = {
	'': "url('./images/normal.svg') no-repeat",
	'': "url('./images/waring.svg') no-repeat",
	'': "url('./images/error.svg') no-repeat"
}
let clickTasks = [];

@observer
class Requirement extends React.Component {
	constructor(props) {
		super(props);
		this.store = new RequirementStore();
		this.state = {
			productData: [{name: '', value: ''}, {name: '', value: ''},{name: '', value: ''}, {name: '', value: ''}, {name: '', value: ''}, {name: '', value: ''}],  // 
			leftSearchData: ['', '', '', ''], // 
			search: [{name: '', value: true}, {name: '', value: false}], // 
			toggle: true, // 
			addVisible: true, // 
			editVisible: false, // 
			readData: {   // 
				visible: false,
				data: {}
			}
		}
	}

	componentDidMount() {
		this.getTableData();
	}

	componentWillUnmount() {

	}

	// 
	handleClick = async (param) => {
		const {addVisible} = this.state;
		if (addVisible == false) return false;
		const clickTask = _.debounce(() => {

			// 
			requestPost('getRequirement', Config.management.getRequirement, Schemas.requirement.getRequirement, {id: param.id}).then((res) => {
				const data = res;
				this.store.readVisible = true;
				this.store.readData = Object.assign({},
					{
						"id": data.id,
						"title": data.title,
						"category": data.category,
						"level": data.level,
						"demandName": data.demandName,
						"demandPhone": data.demandPhone,
						"demandStation": data.demandStation,
						"description": data.description,
						"product": data.product,
						"attachments": data.attachments !== null ? data.attachments.map((item, index) => {
							return Object.assign(item, {
								'uid': item.id,
								'name': item.fileName,
								'status': 'done',
								'url': item.filePath
							})
						}) : []
					})
			});

			clickTasks = [];
		}, 200);
		clickTasks.push(clickTask);
		clickTask();
	}

	// 
	handleDoubleClick = async () => {
		if (clickTasks.length > 0) {
			_.map(clickTasks, (task) => task.cancel());
		}
	}

	getTableData = () => {
		// 
		requestPost('getRequirementPage', Config.management.getRequirementPage, Schemas.requirement.getRequirementPage, {
			all: this.store.all,
			text: this.store.text,
			product: this.store.product,
			status: mobx.toJS(this.store.status),
			pageIndex: this.store.pageIndex,
			pageSize: this.store.pageSize
		}).then((res) => {
			const data = res;
			this.store.tableData = Object.assign([], data.body.map((item, index) => {
				if (index == 0) {
					return Object.assign(item, {selested: true})
				} else {
					return Object.assign(item, {selested: false})
				}
			}));
			this.store.totalCount = data.totalCount;
			if (mobx.toJS(this.store.tableData).length > 0) {
				// 
				let param = mobx.toJS(this.store.tableData)[0];
				this.handleEdit(param);
			} else {
				return false;
			}
		})
	}

	onChange = async (checkedValues) => {
		this.store.status = Object.assign([], checkedValues);
		this.getTableData();
	}

	onSearch = async (value) => {
		const val = _.trim(value);
		this.store.text = val;
		this.getTableData();
	}

	checkLi = async (value) => {
		this.store.all = value;
		this.getTableData();
	}

	handleToggle = async () => {
		this.setState({
			toggle: !this.state.toggle
		});
	}

//  
	handleAdd = () => {
		this.setState({
			addVisible: true,
			editVisible: false
		}, () => {
			requestPost('addRequirement', Config.management.addRequirement, Schemas.requirement.addRequirement, {}).then((res) => {
				const data = res;
				this.store.addFields = Object.assign({},
					{
						"id": data.id,
						"title": data.title,
						"category": data.category,
						"level": data.level,
						"demandName": data.demandName,
						"demandPhone": data.demandPhone,
						"demandStation": data.demandStation,
						"description": data.description,
						"attachments": data.attachments !== null ? data.attachments.map((item, index) => {
							return Object.assign(item, {
								'uid': item.id,
								'name': item.fileName,
								'status': 'done',
								'url': item.filePath
							})
						}) : []
					})
			})
		});
	}

	// 
	handleEdit = (param) => {
		// 
		if (param.isSave == false) {
			this.setState({
				editVisible: false,
				addVisible: true
			}, () => {
				this.store.requirementId = param.id;
				// 
				requestPost('getRequirement', Config.management.getRequirement, Schemas.requirement.getRequirement, {id: param.id}).then((res) => {
					const data = res;
					this.store.addFields = Object.assign({},
						{
							"id": data.id,
							"title": data.title,
							"category": data.category,
							"level": data.level,
							"demandName": data.demandName,
							"demandPhone": data.demandPhone,
							"demandStation": data.demandStation,
							"description": data.description,
							"product": data.product,
							"attachments": data.attachments !== null ? data.attachments.map((item, index) => {
								return Object.assign(item, {
									'uid': item.id,
									'name': item.fileName,
									'status': 'done',
									'url': item.filePath
								})
							}) : []
						})
				})
			});
		} else {
			// 
			this.setState({
				editVisible: true,
				addVisible: false
			}, () => {
				this.store.requirementId = param.id;
				this.store.statusActive = param.status;

				// 
				requestPost('getRequirement', Config.management.getRequirement, Schemas.requirement.getRequirement, {id: param.id}).then((res) => {
					const data = res;
					this.store.editFields = Object.assign({},
						{
							"id": data.id,
							"title": data.title,
							"category": data.category,
							"level": data.level,
							"demandName": data.demandName,
							"demandPhone": data.demandPhone,
							"demandStation": data.demandStation,
							"description": data.description,
							"product": data.product,
							"attachments": data.attachments !== null ? data.attachments.map((item, index) => {
								return Object.assign(item, {
									'uid': item.id,
									'name': item.fileName,
									'status': 'done',
									'url': item.filePath
								})
							}) : []
						})
				})

				// 
				requestPost('getLastAnalysisByRequirementId', Config.management.getLastAnalysisByRequirementId, Schemas.requirement.getLastAnalysisByRequirementId, {requirementId: param.id}).then((res) => {
					const data = res;
					this.store.anActive = data.id;  // id
					this.store.isSave = data.isSave;  // 
					this.store.analysisFields = Object.assign({},
						{
							"id": data.id,
							"toUserId": data.toUserId,
							"toUserName": data.toUserName,
							"bsa": data.bsa,
							"note": data.note,
							"status": data.status,
							"record": data.record,
							"userId": data.userId,
							"userName": data.userName,
							"createTime": data.createTime,
							"isSave": data.isSave,
							"requirementId": data.requirementId,
							"attachments": data.attachments !== null ? data.attachments.map((item, index) => {
								return Object.assign(item, {
									'uid': item.id,
									'name': item.fileName,
									'status': 'done',
									'url': item.filePath
								})
							}) : []
						}); // 

					if (data.id !== null) {
						//  
						requestPost('getAnalysisRelationPage', Config.management.getAnalysisRelationPage, Schemas.requirement.getAnalysisRelationPage, {
							analysisId: this.store.anActive, pageIndex: 1, pageSize: 50
						}).then((res) => {
							this.store.lisTable = Object.assign([], res.body);
						});
					}
				})

				// 
				requestPost('getAnalysisList', Config.management.getAnalysisList, Schemas.requirement.getAnalysisList, {requirementId: param.id}).then((res) => {
					this.store.analysisList = Object.assign([], res);
				})

			});
		}
	}

// 
	handclickEdit = (param) => {
		let dataArray = mobx.toJS(this.store.tableData).map((item) => Object.assign(item, {selested: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selested: true}))
		this.store.tableData = Object.assign([], dataArray)
		this.handleEdit(param);
	}

// 
	handleNext = () => {
		let dataArray = mobx.toJS(this.store.tableData).map((item) => Object.assign(item, {selested: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == this.store.requirementId
		})
		// +1  
		if ((dindex + 1) === mobx.toJS(this.store.tableData).length) {
			message.warn("!");
			return false;
		}
		const param = dataArray[dindex + 1];
		dataArray.splice(dindex + 1, 1, Object.assign(param, {selested: true}))
		// console.log(dataArray, '')
		this.store.tableData = Object.assign([], dataArray)
		this.store.requirementId = param.id;
		this.handleEdit(param);
	}

	showDeleteConfirm = async (id) => {
		let _this = this;
		confirm({
			title: '?',
			okText: '',
			okType: 'danger',
			cancelText: '',
			onOk() {
				requestPost('delRequirement', Config.management.delRequirement, Schemas.requirement.delRequirement, {
					requirementId: id
				}).then((res) => {
					if (res == true) {
						message.success("")
						//
						_this.getTableData();
					} else {
						message.error('')
					}
				})
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

// 
	handleInfiniteOnLoad = () => {
		let dataArray = mobx.toJS(this.store.tableData);
		if (this.store.totalCount === dataArray.length) {
			return;
		}
		this.store.loading = true;
		this.store.pageIndex = this.store.pageIndex + 1;

		requestPost('getRequirementPage', Config.management.getRequirementPage, Schemas.requirement.getRequirementPage, {
			all: this.store.all,
			text: this.store.text,
			status: mobx.toJS(this.store.status),
			pageIndex: this.store.pageIndex,
			pageSize: this.store.pageSize
		}).then((res) => {
			const data = res;
			dataArray = dataArray.concat(data.body);
			this.store.tableData = Object.assign([], dataArray);
			this.store.loading = false;
		})
	}

	// 
	getSimilarityRequirementPage = () => {
		// requestPost('getSimilarityRequirementPage', Config.management.getSimilarityRequirementPage, Schemas.requirement.getSimilarityRequirementPage, {
		// 	all: this.store.all,
		// 	text: this.store.text,
		// 	word: this.store.word,
		// 	status: mobx.toJS(this.store.status),
		// 	pageIndex: this.store.pageIndex,
		// 	pageSize: this.store.pageSize
		// }).then((res) => {
		// 	this.store.tableData = Object.assign([], res.body);
		// })
	}

	// 
	changePro = (value) => {
		console.log(value)
		this.store.product = value;
		this.getTableData();
	}

	render() {
		const {leftSearchData, search, toggle, addVisible, editVisible, productData} = this.state;

		return (
			<div className="container-fluid clearfix">
				<div className="side-col" style={{width: toggle ? '325px' : '50px'}}>
					<Search
						style={{
							width: '80%',
						}}
						size="middle"
						onSearch={this.onSearch}
						placeholder=""
						className={toggle ? "" : "hidden"}/>
					<span className={toggle ? "require-add" : "fl"} onClick={this.handleAdd}></span>
					<CheckboxGroup options={leftSearchData}
					               className={toggle ? "require-checkbox" : "hidden"}
					               value={mobx.toJS(this.store.status)}
					               onChange={this.onChange}/>
					<div className={toggle ? "tl pl15" : "hidden"}>
						<span className="mr5 f12">:</span><Select
						placeholder="..."
						onChange={this.changePro.bind(this)}
						style={{width: '85px', height:'30px'}}
						value={this.store.product}
						size={"small"}
					>
						{productData.map((item, index) => {
							return (<Option key={'product' + index} value={item.value}>{item.name}</Option>)
						})}
					</Select>
					</div>
					<div className="require-table2">
						<div className="clearfix" style={{marginBottom: '10px'}}>
							<ul className={toggle ? "require-ul fl" : "hidden"}>
								{search.map((item, index) => {
									return (<li key={'li' + index}
									            onClick={this.checkLi.bind(this, item.value)}
									            className={this.store.all == item.value ? "active pointer" : "pointer"}>{item.name}</li>)
								})}
							</ul>
							<div className={toggle ? "fr pointer" : "fl  pointer"} onClick={this.handleToggle}>
								{toggle ? <Icon type="menu-fold"/> : <Icon type="menu-unfold"/>}
							</div>
						</div>
						<div className={toggle ? "" : "hidden"}>
							<Scrollbars
								style={{height: "calc(100vh - 260px)"}}
								autoHide>
								{mobx.toJS(this.store.tableData).length > 0 ?
									<InfiniteScroll
										initialLoad={false}
										pageStart={0}
										loadMore={this.handleInfiniteOnLoad.bind(this)}
										hasMore={!this.store.loading && this.store.hasMore}
										useWindow={false}
									>
										{mobx.toJS(this.store.tableData).length > 0 && mobx.toJS(this.store.tableData).map((item, index) => {
											return (<div className={item.selested == true ? "require-tr require-tr-active" : "require-tr"}
											             onClick={this.handclickEdit.bind(this, item)}
											             onDoubleClick={this.handleDoubleClick.bind(item)}
											             key={'tab-' + index}>
												<div className="pr">
													<span className="require-td" title={item.title}>{item.title}</span>
													<span className="require-edit" title={''}
													      onClick={this.handleClick.bind(this, item)}></span>
													<Icon type="pushpin"
													      title=""
													      className={item.isSave == false ? "" : "hidden"}
													      style={{position: 'absolute', right: '5px', top: '15px', color: 'rgb(217, 0, 27'}}
													/>
													<Icon type="close" onClick={this.showDeleteConfirm.bind(this, item.id)} title={''}
													      className={item.isSave == false ? "require-del" : "hidden"}
													      style={{position: 'absolute', right: '5px', top: '-5px', color: 'rgb(217, 0, 27'}}/>
													<span className="require-lavel"
													      style={{background: item.level ? typeColors[item.level] : 'transparent'}}></span>
												</div>
												<div className="clearfix" style={{marginTop: '5px'}}>
                      <span
	                      className="fl require-title">{moment(item.createTime).format("YYYY-MM-DD HH:mm")}&nbsp;&nbsp;{item.userName}</span>
													<span className="fr require-title mr25">{item.status}</span>
												</div>
											</div>)
										})}
										{this.store.loading && this.store.hasMore && (
											<div className="demo-loading-container">
												<Spin/>
											</div>
										)}
									</InfiniteScroll> : <Empty/>}
							</Scrollbars>
						</div>
					</div>
				</div>
				<div className="main-col" style={{width: toggle ? 'calc(100% - 350px)' : 'calc(100% - 50px)'}}>
					<RequirementAdd
						store={this.store}
						visible={addVisible}
						getTableData={this.getTableData}
						getSimilarityRequirementPage={this.getSimilarityRequirementPage}
					/>
					<RequirementEdit
						store={this.store}
						visible={editVisible}
						handleNext={this.handleNext}
						getTableData={this.getTableData}
					/>
				</div>
			</div>
		)
	}
}


export default Requirement;



