/*
*  store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from 'utils/api';
import Schemas from "@/utils/Schemas/requirement";

class RequirementStore {
  @observable totalCount = 0;
  @observable loading=false;
  @observable hasMore=true;
  @observable pageIndex = 1;
  @observable pageSize = 20;
  @observable status = [];  // checkbox
  @observable product = "";  // 
  @observable all = true;  //  true  false 
  @observable text = '';  // 
  @observable tableData = []; // 
  @observable addFields = {   // 
    "title": "",
    "category": [],
    "level": "",
    "product":"",
    "demandName": "",
    "demandPhone": "",
    "demandStation": "",
    "description": "",
    "attachments": [],
    "id":""
  }
  @observable editFields = {};//
  @observable analysisFields= {
    "id": "",
    "toUserId": "",
    "toUserName": "",
    "bsa": "",
    "note": "",
    "status": "",
    "record": "",
    "userId": "",
    "userName": "",
    "createTime": "",
    "isSave": false,
    "requirementId": "",
    "attachments": []
  }; // 
  @observable editAnalysisFields= {}; // 
  @observable analysisList = []; // 
  @observable anActive = '';  // 
  @observable rolesPageIndex = 1; // 
  @observable rolesPageSize = 20; // 
  @observable rolesTotalCount = 0; // 
  @observable rolesTable = []; // 
  @observable lisTable = []; // 
  @observable num = null; // 
  @observable requirementId = '' ; // id
  @observable isSave = false ; //  false  true 
  @observable statusActive = ''; // 
  @observable readVisible = false;  // 
  @observable readData = {};  // 
  @observable word = '';  // 

  // 
  @action
  saveRequirement(param) {
     return requestPost('saveRequirement', Config.management.saveRequirement, Schemas.requirement.saveRequirement, param)
  }

  // 
  @action
  saveAnalysis(param) {
    return requestPost('saveAnalysis', Config.management.saveAnalysis, Schemas.requirement.saveAnalysis, param)
  }
}


export default RequirementStore;



