let Config = {
// 
  requirement: {
    addRequirement: `mutation {
		addRequirement
		{
			id
			number
			seq
			title
			category
			level
			demandName
			demandPhone
			demandStation
			description
			status
			toUserName
			userName
			userId
			createTime
			isSave
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
			product
		}
	}`, //  Requirement

    saveRequirement: `mutation($type: Int, $requirement: InputRequirement) {
		saveRequirement(type: $type, requirement: $requirement)
		{
			id
			number
			seq
			title
			category
			level
			demandName
			demandPhone
			demandStation
			description
			status
			toUserName
			userName
			userId
			createTime
			isSave
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
			product
		}
	}`, //   [0,1]

    addAnalysis: `mutation($requirementId: String) {
		addAnalysis(requirementId: $requirementId)
		{
			id
			toUserId
			toUserName
			bsa
			note
			status
			record
			userId
			userName
			createTime
			isSave
			requirementId
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
		}
	}`, //  Analysis

    saveAnalysis: `mutation($type: Int, $analysis: InputAnalysis) {
		saveAnalysis(type: $type, analysis: $analysis)
		{
			id
			toUserId
			toUserName
			bsa
			note
			status
			record
			userId
			userName
			createTime
			isSave
			requirementId
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
		}
	}`, //   [0,1]

    addAttachment: `mutation($attachment: InputAttachment) {
		addAttachment(attachment: $attachment)
		{
			id
			fileName
			fileId
			type
			sourceId
			filePath
		}
	}`, //  Type 1, 2

    delAttachment: `mutation($id: String) {
		delAttachment(id: $id)
	}`, // 

    addAnalysisRelation: `mutation($requirementIds: [String], $analysisId: String, $relation: String) {
		addAnalysisRelation(requirementIds: $requirementIds, analysisId: $analysisId, relation: $relation)
	}`, // 

    delAnalysisRelation: `mutation($id: String) {
		delAnalysisRelation(id: $id)
	}`, // 

    getAnalysisRelationPage: `query($analysisId: String, $pageIndex: Long, $pageSize: Long) {
		getAnalysisRelationPage(analysisId: $analysisId, pageIndex: $pageIndex, pageSize: $pageSize)
		{
			body
			{
				requirementId
				title
				relation
				id
			}
			pageIndex
			pageSize
			totalCount
			pageCount
		}
	}`, // 

    getAnalysis: `query($id: String) {
		getAnalysis(id: $id)
		{
			id
			toUserId
			toUserName
			bsa
			note
			status
			record
			userId
			userName
			createTime
			isSave
			requirementId
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
		}
	}`, // 

    getAnalysisList: `query($requirementId: String) {
		getAnalysisList(requirementId: $requirementId)
			{
			id
			name
			isSave
		}
	}`, // 

    getLastAnalysisByRequirementId: `query($requirementId: String) {
		getLastAnalysisByRequirementId(requirementId: $requirementId)
		{
			id
			toUserId
			toUserName
			bsa
			note
			status
			record
			userId
			userName
			createTime
			isSave
			requirementId
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
		}
	}`, // 

    getRequirement: `query($id: String) {
		getRequirement(id: $id)
		{
			id
			number
			seq
			title
			category
			level
			demandName
			demandPhone
			demandStation
			description
			status
			toUserName
			userName
			userId
			createTime
			isSave
			attachments
			{
				id
				fileName
				fileId
				type
				sourceId
				filePath
			}
			product
		}
	}`, // 

    getRequirementPage: `query($all: Boolean, $text: String, $product: String, $status: [String], $pageIndex: Long, $pageSize: Long) {
		getRequirementPage(all: $all, text: $text, product: $product, status: $status, pageIndex: $pageIndex, pageSize: $pageSize)
		{
			body
			{
				id
				number
				seq
				title
				category
					level
				demandName
				demandPhone
				demandStation
				description
				status
				toUserName
				userName
				userId
				createTime
				isSave
				attachments
					{
					id
					fileName
					fileId
					type
					sourceId
					filePath
				}
				product
			}
			pageIndex
			pageSize
			totalCount
			pageCount
		}
	}`, // 

    delRequirement: `mutation($requirementId: String) {
		delRequirement(requirementId: $requirementId)
	}`, // 

    getRequirementHistoryPage: `query($text: String, $status: [String], $pageIndex: Long, $pageSize: Long) {
		getRequirementHistoryPage(text: $text, status: $status, pageIndex: $pageIndex, pageSize: $pageSize)
		{
			body
			{
				id
				number
				seq
				title
				category
					level
				demandName
				demandPhone
				demandStation
				description
				status
				toUserName
				userName
				userId
				createTime
				isSave
				attachments
					{
					id
					fileName
					fileId
					type
					sourceId
					filePath
				}
				product
			}
			pageIndex
			pageSize
			totalCount
			pageCount
		}
	}`, // 

    splitRequirement: `mutation($num: Int, $requirementId: String) {
		splitRequirement(num: $num, requirementId: $requirementId)
	}`, // 

    finishRequirement: `mutation($requirementId: String) {
		finishRequirement(requirementId: $requirementId)
	}`, // 

    getSimilarityRequirementPage: `query($all: Boolean, $text: String, $word: String, $status: [String], $pageIndex: Long, $pageSize: Long) {
		getSimilarityRequirementPage(all: $all, text: $text, word: $word, status: $status, pageIndex: $pageIndex, pageSize: $pageSize)
		{
			body
			{
				id
				number
				seq
				title
				category
					level
				demandName
				demandPhone
				demandStation
				description
				status
				toUserName
				userName
				userId
				createTime
				isSave
				attachments
					{
					id
					fileName
					fileId
					type
					sourceId
					filePath
				}
				product
			}
			pageIndex
			pageSize
			totalCount
			pageCount
		}
	}`, // 

    getUserInfoListByRoleCode: `query($appId: String, $code: String, $pageIndex: Long, $pageSize: Long) {
		getUserInfoListByRoleCode(appId: $appId, code: $code, pageIndex: $pageIndex, pageSize: $pageSize)
		{
			body
			{
				id
				nickname
				realname
				phone
				email
				gender
				avatar
				isActive
				auths
				station
				creadepartment
			}
			pageIndex
			pageSize
			totalCount
			pageCount
		}
	}`, // code

  }
}
export default Config;



